.SelectedPokemon {
  position: absolute;
  bottom: 30px;
  z-index: 2000;
  left: 50%;
  width: 300px;
  height: 70px;
  margin-left: -150px;
  background: rgba(254, 254, 254, 1);
  color: #496c6f;
  padding: 0 10px;
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0px 0px 20px rgba(73, 108, 111, 0.2);

  .close {
    position: absolute;
    bottom: -20px;
    left: 50%;
    cursor: pointer;
    background: #258695;
    height: 38px;
    width: 38px;
    color: #258695;
    border-radius: 100%;
    box-shadow: 0px 0px 5px rgba(73, 108, 111, 0.7);
    text-align: center;
    margin-left: -19px;
    &:before {
      content: "\00d7";
      background: #258695;
      color: #9ae7a6;
      border: 1px solid #9ae7a6;
      height: 30px;
      width: 30px;
      position: absolute;
      top: 4px;
      left: 4px;
      width: 30px;
      height: 30px;
      border-radius: 100%;
      text-align: center;
      box-sizing: border-box;
      line-height: 28px;
      font-size: 28px;
      font-weight: 300;
    }
  }
  img {
    float: left;
    margin: 8px 0;
    width: 50px;
  }

  h2 {
    margin: 10px 0 0;
    line-height: 50px;
    padding-left: 57px;
    font-weight: 400;
    font-size: 20px;
  }

  h3 {
    margin-top: 10px;
    float: right;
    .label,
    .value {
      display: block;
      text-align: center;
    }

    .label {
      font-weight: 300;
      font-size: 12px;
      color: #868686;
      text-transform: uppercase;
    }

    .value {
      font-weight: 400;
      color: #496c6f;
      font-size: 24px;
      position: relative;
    }
  }
}
