#PokeMap {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.marker-user {
  margin-left: -7px;
  margin-top: -7px;
  height: 14px;
  width: 14px;
  background: #258695;
  border: 2px solid white;
  border-radius: 100%;
  z-index: 3000 !important;
  box-sizing: border-box;
  position: absolute;
  transition: transform 0.25s ease-out;
}

.marker-user-ping {
  transition: transform 0.25s ease-out;
}

.marker-user-ping:after {
  display: block;
  content: '';
  margin-left: -5px;
  margin-top: -5px;
  height: 10px;
  width: 10px;
  background: #258695;
  animation-timing-function: ease-out;
  position: absolute;
  border-radius: 100%;
  z-index: 2500 !important;
  box-sizing: border-box;
  animation-duration: 2.5s;
  animation-delay: 0s;
  animation-name: ping;
  animation-iteration-count: infinite;
}

@keyframes ping {
  from {
    transform: scale(0,0);
    opacity: 0.5;
  }

  70% {
    opacity: 0;
    transform: scale(7,7);
  }

  100% {
    opacity: 0;
    transform: scale(10,10);
  }
}

.marker-user-accuracy {
  border-radius: 100%;
  z-index: 2000 !important;
  background: #258695;
  opacity: 0.2;
  width: 100px;
  height: 100px;
  margin-top: -50px;
  margin-left: -50px;
  z-index: 2200 !important;
  position: absolute;
  border-radius: 100%;
  transition: all 0.25s ease-out;
  box-sizing: border-box;
}

.marker-pokemon {
  // background: black;
  background-size: 20px;
  opacity: 0.75;
  width: 30px;
  height: 30px;
  cursor: pointer;
  margin-top: -15px;
  margin-left: -15px;

  img {
    height: 30px;
  }
}

.marker-pokemon-selected {
  opacity: 1;
  background-size: 50px;
  width: 60px;
  height: 60px;
  margin-top: -43px;
  margin-left: -43px;
  padding: 10px;
  pointer-events: none;
  z-index: 1000 !important;
  border: 3px solid #7bf1e5;
  border-radius: 50%;
  img {
    height: 50px;
    margin-top: 5px;
    margin-left: 5px;
  }
}
